@import './styles/reset.css';


.start-screen {
    display: flex;
    color: black;
    z-index: 2;
    background-color: rgb(93, 190, 214);
    height: 100vh;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    h1 {
        color: red;
        border-bottom: 3px black solid;
        font-size: 50px;
    }

    h2 {
        color: green;
        border-bottom: 3px black solid;
        font-size: 30px;
    }
}